.footer {
  background-color: #021130;
  padding: 1rem;
  text-align: center;
  margin: 0;
  width: 100%;
  text-transform: capitalize;
  position: fixed;
  bottom: 0;
}
.footer span {
  color: rgb(132, 128, 128);
}
.date {
  color: #bd370e;
  text-decoration: none;
  display: block;
}

.date a:hover {
  text-decoration: underline;
}

@media (min-width: 768px) {
  .footer {
    padding: 3rem;
  }
}
